.header {
    padding: 0;
    text-align: center;
    background-color: @color;
    position: relative;
    .title {
        margin-bottom: 0;
        line-height: 45px;
        margin-top: 0;
        color: white;
        letter-spacing: 3px;
        font-size: 16px;
    }
    .header-left {
        position: absolute;
        width: 60px;
        left: 0;
        top: 0;
        p {
            margin-bottom: 0;
            font-size: 13px;
            line-height: 45px;
            margin-left: 15px;
            color: white;
            display: inline;
        }
        img {
            width: 15px;
            display: inline;
            margin-top: -2px;
        }
    }
    .header-right {
        height: 45px;
        width: 60px;
        text-align: right;
        position: absolute;
        right: 0;
        top: 0;
        img {
            width: 16px;

            margin-right: 15px;
        }
    }
}
.slider {
    padding: 0;
    .am-control-nav {
        li {
            margin: 0 2px;
            float: right;
            a {
                width: 5px;
                height: 5px;
                background-color: white;
            }
        }
    }
    .am-slider-a1 .am-control-nav li a.am-active {
        background-color: @color;
    }
}

.functions {
    padding: 0;
    background-color: white;
    box-shadow: 0px 1px 1px #B7B7B7;
    .am-u-sm-6 {
        padding: 0;
        text-align: center;
        margin: 13px 0;
        p {
            margin: 8px 0 0 0;
            font-size: 15px;
            line-height: 15px;
        }
    }
    .appointment {
        border-right: 1px solid rgb(191, 191, 191);
        .appointment-img {
            width: 1.4rem;
            height: 1.4rem;
            margin: 0 auto;
            background-image: url(../imgs/appointment.png);
            background-size: contain;
            background-repeat: no-repeat;
        }
    }
    .wholesale {
        .wholesale-img {
            width: 1.4rem;
            height: 1.4rem;
            margin: 0 auto;
            background-image: url(../imgs/wholesale.png);
            background-size: contain;
            background-repeat: no-repeat;
        }
    }
}
.activities {
    padding: 0;
    margin-top: 10px;
    .am-u-sm-6 {
        padding: 0;
    }
    .am-u-sm-12 {
        padding: 0;
    }
    .activities-left {
        border-right: 1px solid rgb(191, 191, 191);
        height: 3.9rem;
        background-image: url(../imgs/activities1.png);
        background-size: contain;
        background-repeat: no-repeat;
    }
    .activities-right {
        height: 3.9rem;
        .activities-right-top {
            border-bottom: 1px solid rgb(191, 191, 191);
            height: 1.95rem;
            background-image: url(../imgs/activities2.png);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            background-color: white;
        }
        .activities-right-bottom {
            height: 1.95rem;
            background-image: url(../imgs/activities3.png);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            background-color: white;
        }
    }
}

.recommend {
    padding: 0;
    margin-top: 10px;
    background-color: white;
    padding: 5px 5px 55px 5px;
    .recommend-title {
        font-size: 15px;
        padding: 5px 0 5px 5px;
        line-height: 15px;
        margin: 0;
    }
    .am-u-sm-6 {
        padding: 5px;
        .product-block {
            border: 1px solid #ccc;
            border-radius: 5px;
        }
        img {
            width: 100%;
            border-radius: 5px;
        }
        .product-word {
            padding: 0 8px;
            width: 100%;
            height: 28px;
            overflow: hidden;
            margin-bottom: 5px;
            p {
                font-size: 13px;
                line-height: 14px;
                margin: 0;
            }
        }
        .price-detail {
            padding: 0 8px;
            .price {
                float: left;
                height: 30px;
                p {
                    font-size: 16px;
                    color: @color;
                    margin: 0;
                    line-height: 30px;
                    font-weight: 600;
                }
            }
            .go-to-detail {
                float: right;
                border-radius: 3px;
                background-color: @color;
                width: 67px;
                text-align: center;
                margin-bottom: 5px;
                p {
                    font-size: 13px;
                    color: white;
                    line-height: 30px;
                    margin-bottom: 0;
                }
            }
        }
    }
}
.menu-fixed {
    position: fixed;
    bottom: 0;
    padding: 5px 0;
    background-color: white;
    z-index: 9;
    border-top: 1px solid #ccc;
    box-shadow: 0 -2px 2px #F5F5F5;
    .am-u-sm-3 {
        text-align: center;
        padding: 0;
        
    }
    .icon {
        width:22px;
        height: 22px;
        margin: 0 auto 3px auto;
    }

    .active {
        .home {
            background-image: url(../imgs/home-open.png);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
        }
        .classification {
            background-image: url(../imgs/classification-open.png);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
        }
        .cart {
            background-image: url(../imgs/cart-open.png);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
        }
        .user {
            background-image: url(../imgs/user-open.png);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: 50% 50%;
        }
         p {
            color: @color;
        }
    }
    
    .home {
        background-image: url(../imgs/home-off.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 50% 50%;
    }
    .classification {
        background-image: url(../imgs/classification-off.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 50% 50%;
    }
    .cart {
        background-image: url(../imgs/cart-off.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 50% 50%;
    }
    .user {
        background-image: url(../imgs/user-off.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 50% 50%;
    }
    p {
        font-size: 12px;
        margin: 0;
        line-height: 13px;
        color: #6B6B6B;
    }
}
